/*** Global and page-specific form style ***/

.standard-form {
	[class^=span] {
		margin-bottom: (percentage($margin/$max-width));
	}

	span {
		@include font-size(16,24);
		display: block;
		margin-bottom: 5px;
	}

	.button {
		max-height: 38px;
		line-height: normal;
	}
}

.rich-text {
	.standard-form {
		margin-left: (-(percentage($margin/$max-width)));
		margin-right: (-(percentage($margin/$max-width)));
	}
}

fieldset {
	border: none;
	padding: 0;
	margin: 0;
}

label {
	display: block;
	margin-bottom: 5px;
}

button {
	border: none;
}

input {
	display: block;
	width: 100%;
	padding: 8px;
	@include placeholder-color(rgba(0,0,0,0));

	&:focus {
		outline: 0;
	}

	&[type=radio],
	&[type=checkbox] {
		height: auto;
	}

	&[type=submit] {
		width: auto;
		border: none;
	}
}

input,
select {
	height: 38px;
}

input,
textarea,
select,
button,
.radio-wrapper,
.checkbox-wrapper {
	@include max-mobile() {
		margin-bottom: 15px;
	}
}

textarea {
	width: 100%;
}

.select-wrapper,
select {
	width: 100%;
}

select {
	padding: 8px;
	cursor: pointer;
}

.select-wrapper {
	position: relative;

	// Included in mark-up for accessibility
	label {
		visibility: hidden;
		height: 0;
		margin: 0;
	}

	.select-arrow {
		position: absolute;
		right: 5px; /* for border */
		bottom: 6px; /* for border */
		width: 26px;
		height: 26px; /* height of select-wrapper, minus 2 for border */
		background-color: $white;
		pointer-events: none;

		&:before {
			content: '\e613';
			vertical-align: middle;
			width: 100%;
			color: $black;
			font-family: IcoMoon;
			@include font-size(26,26); /* line-height is height of select-arrow */
		}

		/** NOTE: be sure to watch out for changes in heights based on changes in typograhpy sizes **/
		@include max-mobile() {
			bottom: 21px;
		}
	}

	// <= IE10
	html.no-webgl.no-flexboxlegacy & {
		.select-arrow {
			display: none;
		}
	}
}

input[type=radio],
input[type=checkbox] {
	position: absolute;
	left: -9999px;
}

.radio-wrapper,
.checkbox-wrapper {
	label {
		position: relative;
		padding-left: 30px;

		&:before {
			content: '';
			position: absolute;
			width: 15px;
			height: 15px;
			top: 3px;
			left: 0;
			background-color: $white;
		}
	}

	// Inline styles
	&.inline {
		label {
			display: inline-block;
			margin-right: 30px;
		}
	}
}

.radio-wrapper {
	label {
		&:before {
			@include border-radius(100, "%");
			border: 1px solid $grey-2;
		}
	}

	input[type=radio]:checked + label {
		&:before {
			background-color: $ed-blue;
		}
	}
}

.checkbox-wrapper {
	label {
		&:before {
			content: '\e616';
			color: $ed-blue;
			font-family: IcoMoon;
			@include font-size(17,17);
		}
	}

	input[type=checkbox]:checked + label {
		&:before {
			content: '\e615';
		}
	}

	// <= IE 11
	html.no-flexboxlegacy & {
		label {
			&:before {
				top: 11px;
			}
		}
	}
}


.search-icon {
	background-color: transparent;
	background-image: url(../images/icons/Search_Icon.png);
	background-position: right center;
	background-repeat: no-repeat;
	background-size: 21px;
	height: 21px;
	width: 21px;
	cursor: pointer;
	position: relative;
	z-index: 2;
	float: right;

	&.active {
		background-image: none;

		&:before {
			content: "\e610";
			font-family: IcoMoon;
			color: $white;
			@include font-size(21);
			position: absolute;
			top: 0;
			left: 0;
		}
	}
}

.search-form {
	position: relative;
	@include placeholder-color(rgba(0,0,0,0));

	// Included in mark-up for accessibility
	label {
		visibility: hidden;
		height: 0;
	}

	fieldset {
		border: none;
		padding: 0;
		margin: 0;
	}

	input {
		float: right;
		background-color: transparent;
		background-image: url(../images/icons/Search_Icon.png);
		background-position: right center;
		background-repeat: no-repeat;
		background-size: 21px;
		font-size: 16px;
		height: 21px;
		min-height: 0;
		line-height: 21px !important;
		padding-top: 0;
		padding-left: 0;
		padding-bottom: 0;
		padding-right: 25px;
		border: none;
		border-left: 1px solid $black;
		width: 31px;
		cursor: pointer;
		@include transition(all 0.3s ease);
		color: rgba(255,255,255,0);
		position: relative;
		z-index: 2;

		&:focus {
			width: 150px;
			border-left: none;
			color: $black;
			z-index: 0;
		}

		@include max-mobile() {
			font-size: 16px;
		}
	}

	input[type=submit] {
        position: absolute;
        width: 31px;
        height: 21px;
        opacity: 0;
        top: 0;
        right: 0;
        padding: 0;
        margin: 0;
        border: none;
        z-index: 1;
    }

	&:after {
		@include cf();
	}
}

.large-search-form {
	@include flex();
	@include flex-align(center);
	@include flex-justify(center);
	position: fixed;
	height: 100%;
	width: 100%;
	top: 0;
	right: 0;
	@include transform-origin(100% 0);
	@include transform(scale(0,0));
	background-color: rgba(0,0,0,0.9);
	@include transition(all 0.5s ease 0.5s);

	// Included in mark-up for accessibility
	label {
		visibility: hidden;
		height: 0;
	}

	// IE9 fix
	html.no-flexbox & {
		display: block;
		padding-top: 40vh;

		fieldset {
			display: block;
			margin: 0 auto;
		}
	}

	fieldset {
		width: 300px;
		border: none;
		border-bottom: 3px solid $white;
		padding: 0 0 5px 0;
		opacity: 0;
		@include transition(opacity 0.3s ease);
		@include placeholder-color(rgba(255,255,255,0.5));

		@include max-mobile() {
			width: 75%;
		}

		input {
			background-color: transparent;
			border: none;
			color: $white;
			float: left;
			height: auto;
			padding: 0;
			$emToCalc: em(21);
			@include calc(width, "100% - " + $emToCalc);

			&[type=submit] {
				background-image: url(../images/icons/Search_Icon.png);
				background-position: right center;
				background-repeat: no-repeat;
				background-size: 21px;
				width: 21px;
				height: 21px;
				text-indent: -2000px;
			}
		}
	}

	&.active {
		@include transform(scale(1,1));
		@include transition(all 0.5s ease);

		fieldset {
			opacity: 1;
			@include transition(opacity 0.3s ease 0.75s);
		}
	}
}
