body {
	position: relative;
	width: 100%;
	left: 0;
	@include transition(left 0.3s ease);
	
	&.open {
		position: fixed;	
		left: 240px;
	}
	
	&.search-active {
		position: fixed;
		width: 100%;
	}
}

* {
	@include box-sizing(border-box);
}

.inline{
	display: inline-block;
}

strong{
	font-weight: bold;
}

ul {
	padding-left: 0;
}

img {
	 max-width: 100%;
	 height: auto;
	 vertical-align: bottom;
}

// Fixes issues with google maps images not showing correctly
