/* Cookie Notification styles */
.cookie-notification {
	background-color: $ed-blue-darker;
	color: $white;
	display: none;
	padding: 10px;
	width: 100%;
	
	@include max-desktop() {
		padding: 10px 20px;
	}
	
	@include max-mobile() {
		padding: 10px 15px;
	}
	
	.row {
		@include flex();
		@include flex-align(center);
		
		// IE9 Fix
		html.no-flexbox & {
			[class^=span] {
				display: block;
				float: left;
				padding: 0;
			}
		}
	}
	
	p {
		margin: 0;
		line-height: 1.5rem;
	}
	
	a {
		color: $white;
		text-decoration: underline;
	}

	.close {
		span {
			display: inline-block;
			padding: 10px 10px 10px 11px;
			font-family: IcoMoon;
			background-color: $ed-blue-dark;
			cursor: pointer;
			position: relative;
			line-height: normal;
			
			&:before {
				content: '\e610';
				font-size: 21px;
				color: $white;						
			}
		}
	}
}