/*** STATIC BUILD ONLY **/
.jump-nav {
	position: fixed;
	bottom: 0;
	right: 0;
	height:auto;
	width: auto;
	width: 200px;
	padding: 0;
	cursor: pointer;
	text-align: right;
	@include transition(bottom 0.3s ease);
	
	ul {
		list-style: none;
		padding: 0;
		margin: 0;
		text-align: left;
		
		li {
			&:first-child {
				a {
					border: none;
					@include border-radius-all(5,0,0,0,px);
				}
			}
		}
	}
	
	&:hover,
	&.active {
		bottom: 0 !important;
	}
	
	.tab {
		display: inline-block;
		color: $white;
		font-size: 16px;
		line-height: 16px;
		padding: 15px;
		background-color: rgba(0,173,239,0.75); // 00adef
		border-bottom: 1px solid rgba(255,255,255,0.75);
		@include border-radius-all(5,0,0,0,px);
	}
	
	a {
		width: 100%;
		display: inline-block;
		font-size: 16px;
		line-height: 16px;
		padding: 15px;
		color: $white;
		text-decoration: none;
		background-color: rgba(0,173,239,0.75); // 00adef
		@include transition(background-color 0.3s ease);
		border-top: 1px solid rgba(255,255,255,0.75);
		
		&:hover {
			background-color: rgba(0,173,239,0.95); // 00adef
		}
	}
}

/*** END STATIC BUILD ONLY ***/


/*** Header Primary Nav ***/

#primary-nav {
	position: static;
	
	ul {
		font-size: 0;
		margin-bottom: 0;
		
		li {
			position: static;
			@include font-size(16);
			margin-right: 20px;
		
            html.no-touch & {
                &:hover {
                    .sub {
                        visibility: visible;
                        opacity: 1;
                    }
                }
            }
		}
	}
	
	/* Effects immediate children only */
	> ul {
		> li {
			display: inline-block;
            
            @include max-tablet() {
                display: block;
                padding: 15px 10px;
                margin: 0;
                position: relative;
                
                &.has-child {
                    &:after {
                        content: '';
                        position: absolute;
                        top: 50%;
                        margin-top: -7px;
                        right: 15px;
                        background-color: none;
                        width: 10px;
                        height: 10px;
                        border-top: 1px solid $black;
                        border-right: 1px solid $black;
                        @include rotate(45deg);
                    }
                }
            }
		}
	}
}

/* Header Primary Nav - Sub Nav */
.sub {
	position: absolute;
	left: 0;
	width: 100%;
	padding: 15px;
	visibility: hidden;
	opacity: 0;
	@include transition(all 0.3s ease);
	
	@include max-desktop() {
		padding: 15px 0;
	}
	
	.row {
		padding: 15px 0;
		background-color: $white;
	}
	
	ul {		
		li {
			display: block;
			
			&:last-child {
				padding: 0;
			}
		}
	}
    
    @include max-tablet() {
        left: 100%;
        opacity: 1;
    }
}

.breadcrumbs {
	position: relative;
	margin-bottom: 40px;
	
	ul {
		li {
			display: inline-block;
			@include font-size(14);
			margin-bottom: 10px;
			
			&:after {
				content: '/';
				position: relative;
				margin-left: 5px;
			}
		}
	}
	
	@include max-mobile() {
		margin-bottom: 20px;
	}
}