/*** Site Header Styles ***/

#site-header {
	background-color: $ed-blue;
	padding: 30px 0;
	position: relative;
	z-index: 1;
	
	@include max-desktop() {
		padding: 30px 20px;
		
		.row {
			position: static;
		}
	}
	
	@include max-mobile() {
		padding: 20px 15px;
	}

	/* Header Nav Container */
	.row {
		.nav-container {
			position: static;
			
			@include max-tablet() {
				position: fixed;
				left: -240px;
				top: 0;
				height: 100%;
				width: 240px;
				overflow-y: auto;
				overflow-x: hidden;
                margin: 0;
				@include transition(left 0.3s ease);
				
				&.open {
					left: 0;
				}
			}
		}
	}

	&.persistant-nav {
		position: fixed;
		top: 0 !important;
		width: 100%;
	}

	&.persistant-nav-mobile {
		z-index: 10;
		position: fixed;
		@include transition(top 0.2s ease-in-out);
		width: 100%;

		&.mobile-visible {
			top: 0 !important;
			z-index: 10;
		}
	}
	
	p {
		margin: 0;
	}
	
	.menu {
		display: none;
		
		.menu-icon {
	        height: 25px;
	        width: 25px;
	        cursor: pointer;
			display: inline-block;
	        
	        span {
	            height: 5px;
	            width: 100%;
	            display: block;
	            background-color: $black;
				@include transition(all 0.3s ease);
	            
	            &.middle {
	                margin: 5px 0;
	            }
	        }
	    }
	}
	
	@include max-tablet() {
		.menu {
			display: block;
			
			.menu-icon {
				&.open {
					span {
						background-color: $white;
					}
					.top {
						@include rotate(45deg);
						@include transform-origin(0 0);
					}
					.middle {
						@include rotate(-45deg);
						@include transform-origin(7px 8px);
					}
					.bottom {
						opacity: 0;
					}
				}
			}
		}
	}
}