// 	@if $property = 'width' {
// 		width: -moz-calc($value);
// 		width: -webkit-calc($value);
// 		width: -o-calc($value);
// 		width: calc($value);
// 	}

// 	@if $property = 'height' {
// 		height: -moz-calc($value);
// 		height: -webkit-calc($value);
// 		height: -o-calc($value);
// 		height: calc($value);
// 	}
// }
@mixin calc($property, $expression) {
	#{$property}: -moz-calc(#{$expression});
  	#{$property}: -webkit-calc(#{$expression});
  	#{$property}: -o-calc(#{$expression});
  	#{$property}: calc(#{$expression});
}


/* Miscellaneous mixins */
@mixin box-sizing($size) {
	-webkit-boz-sizing: $size;
	-moz-box-sizing: $size;
	box-sizing: $size;
}

@mixin font-size($size, $line-height: ($size + 4)) {
	font-size: $size + px;
	font-size: meta-unit($size,rem);
	line-height: meta-unit($line-height,px);
	line-height: meta-unit($line-height,rem);
}

@mixin vertical-gradient($start,$end) {
	background: $end;
	background: -webkit-linear-gradient($start,$end);
	background: linear-gradient($start,$end);
}

@mixin hide-text() {
	text-indent: 110%;
	white-space: nowrap;
	overflow: hidden;
}

@mixin cf() {
	visibility: hidden;
	display: block;
	font-size: 0;
	content: " ";
	clear: both;
	height: 0;
}

@mixin columns($value) {
	-webkit-columns: $value;
	-moz-columns: $value;
	columns: $value;
}

// Allows for 2x images for device ratios 2+
@mixin high-res-bg-image($image, $width, $height){
	@media
	only screen and (-webkit-min-device-pixel-ratio: 2),
	only screen and (   min--moz-device-pixel-ratio: 2),
	only screen and (     -o-min-device-pixel-ratio: 2/1),
	only screen and (        min-device-pixel-ratio: 2),
	only screen and (                min-resolution: 192dpi),
	only screen and (                min-resolution: 2dppx) {
		background-image: url($image);
		background-size: $width $height;
	}
}
